 Rahmen unseres berechtigten Interesses nach Art. 6 Abs. 1 lit. f DSGVO zu löschen oder zu sperren. ")]),_c('p',[_vm._v(" Daten, die zu anderen Zwecken bei uns gespeichert wurden, bleiben hiervon unberührt. ")]),_vm._m(5),_c('h3',[_vm._v("Mailgun")]),_vm._m(6),_c('p',[_vm._v(" Der Versanddienstleister kann die Daten der Empfänger in pseudonymer Form, d.h. ohne Zuordnung zu einem Nutzer, zur Optimierung oder Verbesserung der eigenen Services nutzen, z.B. zur technischen Optimierung des Versandes und der Darstellung der Newsletter oder für statistische Zwecke verwenden. Der Versanddienstleister nutzt die Daten unserer Newsletterempfänger jedoch nicht, um diese selbst anzuschreiben oder um die Daten an Dritte weiterzugeben. ")]),_c('h2',[_vm._v("7. Plugins und Tools")]),_c('h3',[_vm._v("Google Fonts (lokales Hosting)")]),_c('p',[_vm._v(" Diese Seite nutzt zur einheitlichen Darstellung von Schriftarten so genannte Google Fonts, die von Google bereitgestellt werden. Die Google Fonts sind lokal installiert. Eine Verbindung zu Servern von Google findet dabei nicht statt. ")]),_vm._m(7),_c('h3',[_vm._v("Youtube")]),_vm._m(8),_c('h3',[_vm._v("Twitter")]),_vm._m(9),_c('h3',[_vm._v("Instagram")]),_vm._m(10),_c('h3',[_vm._v("HERE Maps")]),_vm._m(11),_c('h2',[_vm._v(" 8. Verarbeitung personenbezogener Daten für die Zahlungsabwicklung ")]),_c('p',[_vm._v(" Für die Bearbeitung Ihres Auftrages arbeiten wir mit den folgenden Dienstleistern zusammen, die uns ganz oder teilweise bei der Abwicklung der abgeschlossenen Verträge unterstützen. Bestimmte personenbezogene Daten werden an diese Dienstleister gemäß den folgenden Informationen übermittelt. ")]),_c('p',[_vm._v(" Soweit Zahlungsdienstleister in Anspruch genommen werden, weisen wir Sie im Folgenden ausdrücklich darauf hin. Rechtsgrundlage für die Übermittlung der Daten ist Art. 6 Abs. 1 Buchstabe b D. Eine Weitergabe Ihrer Zahlungsdaten an das mit der Lieferung beauftragte Kreditinstitut erfolgt im Rahmen der Zahlungsabwicklung, soweit dies zur Zahlungsabwicklung erforderlich ist.Sofern Zahlungsdienstleister in Anspruch genommen werden, verpflichten wir uns ausdrücklich teilen Ihnen dies unten mit. Rechtsgrundlage für die Übermittlung der Daten ist Art. 6 Abs. 1 Buchstabe b GDPR. ")]),_c('h3',[_vm._v("Stripe")]),_vm._m(12),_c('h2',[_vm._v("9. Registrierfunktion")]),_c('p',[_vm._v(" Nutzer können ein Nutzerkonto anlegen. Im Rahmen der Registrierung werden die erforderlichen Pflichtangaben den Nutzern mitgeteilt und auf Grundlage des Art. 6 Abs. 1 lit. b DSGVO zu Zwecken der Bereitstellung des Nutzerkontos verarbeitet. Zu den verarbeiteten Daten gehören insbesondere die Login-Informationen (Name, Passwort sowie eine E-Mailadresse). Die im Rahmen der Registrierung eingegebenen Daten werden für die Zwecke der Nutzung des Nutzerkontos und dessen Zwecks verwendet. ")]),_c('p',[_vm._v(" Die Nutzer können über Informationen, die für deren Nutzerkonto relevant sind, wie z.B. technische Änderungen, per E-Mail informiert werden. Wenn Nutzer ihr Nutzerkonto gekündigt haben, werden deren Daten im Hinblick auf das Nutzerkonto, vorbehaltlich einer gesetzlichen Aufbewahrungspflicht, gelöscht. Es obliegt den Nutzern, ihre Daten bei erfolgter Kündigung vor dem Vertragsende zu sichern. Wir sind berechtigt, sämtliche während der Vertragsdauer gespeicherten Daten des Nutzers unwiederbringlich zu löschen. ")]),_c('p',[_vm._v(" Im Rahmen der Inanspruchnahme unserer Registrierungs- und Anmeldefunktionen sowie der Nutzung des Nutzerkontos, speichern wir die IP-Adresse und den Zeitpunkt der jeweiligen Nutzerhandlung. Die Speicherung erfolgt auf Grundlage unserer berechtigten Interessen, als auch der Nutzer an Schutz vor Missbrauch und sonstiger unbefugter Nutzung. Eine Weitergabe dieser Daten an Dritte erfolgt grundsätzlich nicht, außer sie ist zur Verfolgung unserer Ansprüche erforderlich oder es besteht hierzu besteht eine gesetzliche Verpflichtung gem. Art. 6 Abs. 1 lit. c. DSGVO. Die IP-Adressen werden spätestens nach 7 Tagen anonymisiert oder gelöscht. ")]),_c('h2',[_vm._v("10. Kurzbewerbungsfunktion")]),_c('p',[_vm._v(" Arbeitgeber können im Rahmen der Schaltung einer Stellenanzeige die Kurzbewerbungsfunktion aktivieren. BewerberInnen haben die Möglichkeit im Kurzbewerbungsverfahren einige Fragen zu beantworten Ihre Kontaktdaten zu hinterlegen. Die dabei aufgenommenen Daten werden direkt an den entsprechenden Arbeitgeber aus der entsprechenden Stellenanzeige weitergeleitet und nicht von MFA mal anders gespeichert. Dieser Weiterleitung muss von den BewerberInnen aktiv zugestimmt werden. ")]),_c('h2',[_vm._v(" 11. Verarbeitung personenbezogener Daten für Bewerberinnen und Bewerber ")]),_vm._m(13),_c('p',[_vm._v(" Wenn Sie sich bei MFA mal anders bewerben, werden grundsätzlich nur solche Daten verarbeitet, die Sie uns im Rahmen der Bewerbung zur Verfügung gestellt haben. In der Regel verarbeiten wir so Ihre Kontaktdaten (Name, Vorname, E-Mail-Adresse) und persönliche Informationen, die sich aus dem Anschreiben und dem Lebenslauf (Bewerbungsdaten) ergeben. ")]),_vm._m(14),_c('p',[_vm._v(" Es besteht keine gesetzliche oder vertragliche Pflicht zur Bereitstellung von Daten. Im Rahmen Ihrer Bewerbung sollen Sie nur diejenigen personenbezogenen Daten bereitstellen, die für die Aufnahme und Durchführung der Bewerbung erforderlich sind. Ohne diese Daten werden wir jedoch Ihre Aufnahme in den Bewerbungsprozess ablehnen müssen. ")]),_vm._m(15),_c('p',[_vm._v(" Wir verarbeiten Ihre Daten, um Ihre Eignung für die ausgeschriebene Stelle oder ggf. eine andere offene Position in unserem Unternehmen zu prüfen und um das Bewerbungsverfahren durchzuführen. ")]),_vm._m(16),_c('p',[_vm._v(" Die Rechtsgrundlage für die Verarbeitung Ihrer personenbezogenen Daten in diesem Bewerbungsverfahren ist Art. 88 DSGVO i.V.m. § 26 BDSG. Danach ist die Verarbeitung der Daten zulässig, die im Zusammenhang mit der Entscheidung über die Begründung eines Beschäftigungsverhältnisses erforderlich sind. ")]),_c('p',[_vm._v(" Insbesondere Daten, die wir über das Bewerbungsverfahren hinausgehend verarbeiten, werden i.d.R. aufgrund des Art. 6 Abs. 1 S. 1 lit. c) DSGVO verarbeitet. Dies ist der Fall, wenn wir gesetzlich verpflichtet sind, Ihre Daten z.B. aus steuerrechtlichen Gründen weiter zu verarbeiten (z.B. Erstattung der Anreisekosten). ")]),_vm._m(17),_c('p',[_vm._v(" Ihre Bewerberdaten werden nach Eingang Ihrer Bewerbung von der Personalabteilung und den entsprechenden Führungskräften gesichtet. Geeignete Bewerbungen werden dann intern an die Abteilungsverantwortlichen für die jeweils offene Position weitergeleitet. Im Unternehmen haben grundsätzlich nur die Personen Zugriff auf Ihre Daten, die dies für den ordnungsgemäßen Ablauf unseres Bewerbungsverfahrens benötigen. ")]),_vm._m(18),_c('p',[_vm._v(" Daten aus Ihrer Bewerbung werden im Falle einer Absage spätestens nach einer Frist von 6 Monaten gelöscht. Selbstverständlich können Sie Ihre Bewerbung auch zurückziehen und wir löschen Ihre personenbezogenen Daten unverzüglich, sofern der Löschung keine gesetzlichen Aufbewahrungsfristen entgegenstehen. ")]),_c('p',[_vm._v(" Bei einer Einwilligung zu längerfristiger Speicherung in unserem Bewerberpool gilt die mit Ihnen vereinbarte Speicherdauer. Diese beträgt in der Regel 2 Jahre. ")]),_c('p',[_vm._v(" Bei einer Einstellung speichern wir Ihre Bewerbungsunterlagen und führen diese in die Personalakte über. ")]),_vm._m(19),_c('p',[_vm._v(" Eine Übermittlung Ihrer Bewerberdaten in ein Drittland findet grundsätzlich nicht statt. Ausnahmsweise kann dies bei bestimmten Programmen der Fall sein, wenn wir diese zur Bewerberverwaltung benutzen. ")]),_vm._m(20),_c('p',[_vm._v(" Ihre Rechte ergeben sich aus den Art. 15 ff. DSGVO. Unter anderem haben Sie: ")]),_vm._m(21),_c('p',[_vm._v(" Zusätzlich können Sie in dem Fall, dass die Verarbeitung einem berechtigtem Interesse nach Art. 6 Abs. 1 S. 1 lit. f) der Verarbeitung nach Art. 21 DSGVO widersprechen. Jedoch werden die personenbezogenen Daten nur dann nicht mehr verarbeitet, wenn wir keine schutzwürdigen Gründe für die Verarbeitung nachweisen können, die Ihre Rechts- und Freiheitsinteressen überwiegen. Unbeschadet dessen bleibt Ihnen stets die Möglichkeit sich bei der zuständigen Datenschutzaufsichtsbehörde zu beschweren (Art. 77 DSGVO i.V.m. § 19 BDSG) oder einen gerichtlichen Rechtsbehelf einzulegen (Art. 78 DSGVO). ")]),_c('h2',[_vm._v("12. Teilnahme an Affiliate-Partnerprogrammen")]),_c('p',[_vm._v(" Innerhalb unseres Onlineangebotes setzen wir auf Grundlage unserer berechtigten Interessen (d.h. Interesse an der Analyse, Optimierung und wirtschaftlichem Betrieb unseres Onlineangebotes) gem. Art. 6 Abs. 1 lit. f DSGVO branchenübliche Trackingmaßnahmen ein, soweit diese für den Betrieb des Affiliatesystems erforderlich sind. Nachfolgend klären wir die Nutzer über die technischen Hintergründe auf. ")]),_c('p',[_vm._v(" Die von unseren Vertragspartnern angebotene Leistungen können auch auf anderen Webseiten beworben und verlinkt werden (sog. Affiliate-Links oder After-Buy-Systeme, wenn z.B. Links oder Leistungen Dritter nach einem Vertragsschluss angeboten werden). Die Betreiber der jeweiligen Webseiten erhalten eine Provision, wenn Nutzer den Affiliate-Links folgen und anschließend die Angebote wahrnehmen. ")]),_c('p',[_vm._v(" Zusammenfassend, ist es für unser Onlineangebot erforderlich, dass wir nachverfolgen können, ob Nutzer, die sich für Affiliate-Links und/oder die bei uns verfügbaren Angebote interessieren, die Angebote anschließend auf die Veranlassung der Affiliate-Links oder unserer Onlineplattform, wahrnehmen. Hierzu werden die Affiliate-Links und unsere Angebote um bestimmte Werte ergänzt, die ein Bestandteil des Links oder anderweitig, z.B. in einem Cookie, gesetzt werden können. Zu den Werten gehören insbesondere die Ausgangswebseite (Referrer), Zeitpunkt, eine Online-Kennung der Betreiber der Webseite, auf der sich der Affiliate-Link befand, eine Online-Kennung des jeweiligen 